.recurse-bg {
  background:
    -webkit-gradient(
      linear,
      left top, left bottom,
      from(rgba(0, 0, 0, 0.65)),
      to(rgba(0, 0, 0, 0.65))
    ),
    url('../../images/calculator.jpeg') no-repeat center center fixed;
  background:
    -webkit-linear-gradient(
      rgba(0, 0, 0, 0.65),
      rgba(0, 0, 0, 0.65)
    ),
    url('../../images/calculator.jpeg') no-repeat center center fixed;
  background:
    -o-linear-gradient(
      rgba(0, 0, 0, 0.65),
      rgba(0, 0, 0, 0.65)
    ),
    url('../../images/calculator.jpeg') no-repeat center center fixed;
  background:
    linear-gradient(
      rgba(0, 0, 0, 0.65),
      rgba(0, 0, 0, 0.65)
    ),
    url('../../images/calculator.jpeg') no-repeat center center fixed;
  background-size: cover;

  padding-top: 50px;
  min-height: calc(100vh - 190px);
}



